// import React from 'react'
import Task from './components/Tasks'
import Header from './components/Header'
import { useState } from 'react'
import AddTask from './components/AddTask'



const App= ({onAdd})  => {
  const name = 'seo'
  const x = true
  const [showAddTask, setShowAddTask] =useState(false)
  const [tasks, setTasks] = useState([
        {
            id:1,
            text: 'Doctor Appointment',
            day: 'Feb 15 at 9pm',
            reminder: true,
        },
        {
            id: 2,
            text: 'school HomeWork',
            day: 'Feb 16 at 7pm',
            reminder: false,
        },
        {
            id:3,
            text: 'buy car',
            day: 'Feb 16 at 7pm',
            reminder: true,
        }
    ])
    //Add task

    const addTask = (task) => {
      const id = Math.floor(Math.random() * 10000) + 1
      const newTask ={id, ...task}
      setTasks([...tasks, newTask])

    }
    //Delete task

    const deleteTask = (id) => {
      setTasks(tasks.filter((task) => task.id !== id))
    }
    // Toggle Reminder
    const toggleReminder = (id) => {
      setTasks(
        tasks.map((task) => 
        task.id === id ? {...task, reminder: 
          !task.reminder} : task))
    }
  return (
    <div className="container">
      {/* <h1>HELLO FROM NEF</h1>
      <h2>dynamic data say hello to <b>{name}</b></h2>
      <h2>traneryopretors {x? 'yes':'No'}</h2> */}
      <Header onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask}/>  
      {showAddTask && <AddTask onAdd={addTask}/>}
      {tasks.length > 0 ? (<Task tasks={tasks} onDelete =
        {deleteTask} onToggle={toggleReminder}/> ) : ('no tasks')}
      {/* <Header title={1}/> */}
      {/* 0.default above value and if need dynamin value then write with that header tittle='hee or any thing as per your requrments' */}
    </div>
  );
}
// class App extends React.Component {
//   render(){
//     return <h1>Hello from a class</h1>
//   }
// }
//it's for class only
export default App;
